import React, { useEffect, useRef } from "react";

export default function Preloader() {

  const preload = useRef();
  const preloadPersent = useRef();

  useEffect(() => {
    const preAnim = document.querySelector('.pre-anim');
    const bfix = document.body;
    const img = document.images;
    const img_tc = img.length;
    let img_lc = 0;

    for (let i = 0; i < img_tc; i++) {
      let img_cl = new Image();
      img_cl.onload = img_ld;
      img_cl.onerror = img_ld;
      img_cl.src = img[i].src;
    };

    if (img_tc === 0) {
      preaAnim()
    };

    function img_ld() {
      img_lc++;
      preloadPersent.current.innerText = ((100 / img_tc) * img_lc) << 0;
      if (img_lc >= img_tc) {
        preaAnim()
      }
    }

    function preaAnim() {

      const loaded = (id, clss, time) => setTimeout(() => id.classList.add(clss), time);
      loaded(preload.current, 'act', 500);
      if (preAnim) {
        loaded(preAnim, 'before-act', 500);
        loaded(preAnim, 'act', 1900);
      }
      bfix.classList.add('act');
      setTimeout(() => {
        preload.current.remove();
        if (preAnim) {
          preAnim.classList.remove('before-act');
          preAnim.classList.remove('act');
        }
        bfix.classList.remove('act');
      }, 3000);
    }
  }, []);

  return (
    <div className="preloader" ref={preload}>
      <div className="preloader-a"></div>
      <h4 className="preloader-i" ref={preloadPersent}>
      </h4>
    </div>
  )

};
