import React from "react";
import './App.css';
import { Switch, Route, Link, useLocation, useHistory } from "react-router-dom";
import { BrowserView, MobileView } from "react-device-detect";

import Preloader from "./hooks/preloader";

import Home from "./pages/home/Home";
import About from "./pages/about/About";
import Blog from "./pages/blog/Blog";
import IdPage from "./pages/blog/IdPage";
import Contact from "./pages/contact/Contact";

import Navigation from "./components/navigation/Navigation";
import { CustomCursor } from "./components/CustomCursor/CustomCursor";

import { AnimatePresence } from "framer-motion";
import { PageMotion } from "./components/PageMotion";


const App = () => {

  const locationZ = useLocation();

  return (
    <>
      <Preloader />
      {/* <div className="page-load">Loading</div> */}
      <main id="main-box">
        <BrowserView>
          <CustomCursor />
        </BrowserView>
        <Navigation />
        <AnimatePresence exitBeforeEnter initial={false}>
          <Switch key={locationZ.pathname} location={locationZ}>
            <Route exact path="/contact">
              <Page5 />
            </Route>
            <Route exact path="/blog/p-n/:id">
              <Page4 />
            </Route>
            <Route exact path="/blog">
              <Page3 />
            </Route>
            <Route exact path="/about">
              <Page2 />
            </Route>
            <Route exact path="/">
              <Page1 />
            </Route>
            <Route component={NoMatchPage} />
          </Switch>
        </AnimatePresence>
      </main>
      <MobileView>
        <div className="rotate-device">
          <div />
          <div><h4>Please, rotate your device</h4></div>
        </div>
      </MobileView>
    </>
  );
}

const Page1 = () => {
  return (
    <PageMotion>
      <Link to="/" />
      <Home />
    </PageMotion>
  );
}
const Page2 = () => {
  return (
    <PageMotion>
      <Link to="/about" />
      <About />
    </PageMotion>
  )
}
const Page3 = () => {
  return (
    <PageMotion>
      <Link to="/blog" />
      <Blog />
    </PageMotion>
  )
}
const Page4 = () => {
  return (
    <PageMotion>
      <Link to="/blog/p-n/:id" />
      <IdPage />
    </PageMotion>
  )
}
const Page5 = () => {
  return (
    <PageMotion>
      <Link to="/contact" />
      <Contact />
    </PageMotion>
  )
}
export const NoMatchPage = () => {
  const history = useHistory();
  const currentUrl = () => {
    const url = window.location.href || document.URL;
    return url
  }
  const style = {
    notfound: { userSelect: 'none', position: 'fixed', left: 0, top: 0, width: '100%', height: '100%', minHeight: '100vh', padding: '0 5%', background: '#0d0d06', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', color: '#f9f9f9', textAlign: 'center', zIndex: 1 },
    color: { color: '#bd9165' },
    text: { textTransform: 'uppercase' }
  }
  return (
    <div style={style.notfound}>
      <h2>404 Not found</h2>
      <h4>sorry, page not found</h4>
      <p style={style.text}>bad URL - <span style={style.color}>{currentUrl()}</span></p>
      <button data-crsr-zoomout onClick={() => history.goBack()}>BACK</button>
    </div>
  )
}

export default App;