import { isBrowser } from "react-device-detect";

export const HoverCursor = () => {
  if (isBrowser) {
    const dotOutline = document.querySelector('.cursor-dot-outline');
    const dataZoomIn = document.querySelectorAll('[data-crsr-zoomin]');
    const dataZoomOut = document.querySelectorAll('[data-crsr-zoomout]');
    const dataEye = document.querySelectorAll('[data-crsr-eye]');
    const dataBack = document.querySelectorAll('[data-crsr-back]');
    const dataMore = document.querySelectorAll('[data-crsr-more]');
    const dataNext = document.querySelectorAll('[data-crsr-next]');
    const dataPrev = document.querySelectorAll('[data-crsr-prev]');

    const cDotZoomEnter = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
      dotOutline.style.backgroundColor = 'transparent';
      dotOutline.style.border = "solid .2vw #f9f9f9"
    }
    const cDotZoomLeave = () => {
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
      dotOutline.style.backgroundColor = '#f9f9f9';
      dotOutline.style.border = "solid 0 transparent"
    }
    const cDotZoomOutEnter = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(0)";
    }
    const cDotZoomOutLeave = () => {
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
    }
    const cDotEnterBack = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
      dotOutline.classList.add('cursor-back');
    }
    const cDotLeaveBack = () => {
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
      dotOutline.className = 'cursor-dot-outline';
    }
    const cDotEnterEye = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.classList.add('cursor-eye');
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
    }
    const cDotLeaveEye = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
    }
    const cDotEnterMore = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.classList.add('cursor-more');
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
    }
    const cDotLeaveMore = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
    }
    const cDotEnterNext = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.classList.add('cursor-next');
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
    }
    const cDotLeaveNext = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
    }
    const cDotEnterPrev = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.classList.add('cursor-prev');
      dotOutline.style.transform = "translate(-50%, -50%) scale(1)";
    }
    const cDotLeavePrev = () => {
      dotOutline.className = 'cursor-dot-outline';
      dotOutline.style.transform = "translate(-50%, -50%) scale(.1)";
    }

    const animCursor = (cur, enter, leave) => {
      cur.forEach(arr => {
        arr.addEventListener('mouseenter', enter);
        arr.addEventListener('mouseleave', leave);
      });
    }

    animCursor(dataZoomIn, cDotZoomEnter, cDotZoomLeave);
    animCursor(dataZoomOut, cDotZoomOutEnter, cDotZoomOutLeave);
    animCursor(dataEye, cDotEnterEye, cDotLeaveEye);
    animCursor(dataBack, cDotEnterBack, cDotLeaveBack);
    animCursor(dataMore, cDotEnterMore, cDotLeaveMore);
    animCursor(dataNext, cDotEnterNext, cDotLeaveNext);
    animCursor(dataPrev, cDotEnterPrev, cDotLeavePrev);
    
  }
}