import React from "react";
import { Link } from "react-router-dom";
import { useDataJson, styleDataJson, blogItems } from "../../../hooks/useDataJson";

import { HoverCursor } from "../../../components/CustomCursor/CursorHoverSeiitngs";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const animaHeaderElements = () => {

  // ----------- animation header elements

  HoverCursor();

  const imageHeaderAnim = document.querySelector('.img-cont');
  const imgContAnim = document.querySelector('.img-cont-anim');
  const titleHeaderAnim = document.querySelector('.title-header-anim');
  const pageContent = document.querySelector('.page-content');

  if (imageHeaderAnim) {
    setTimeout(() => {
      pageContent.style.visibility = 'hidden'
      pageContent.style.opacity = 0;
      titleHeaderAnim.style.opacity = 0;
      document.body.classList.add('act');

      gsap.fromTo(imageHeaderAnim, 3, { scale: 1.3 }, { scale: 1, ease: "power2" });
      gsap.to(imgContAnim, .8, { height: '50vh', ease: "power2", delay: .8 });
      gsap.fromTo(titleHeaderAnim, 1.3, { x: '8vw', opacity: 0 }, { x: '0', opacity: 1, ease: "power2", delay: 1.4 });
      gsap.to(pageContent, .8, { visibility: 'visible', opacity: 1, ease: "power2", delay: 2 });

      const welcome = gsap.timeline({
        scrollTrigger: {
          trigger: ".sth-scrl",
          scroller: ".sth-scrl",
          scrub: true,
          start: "top top",
          end: "+=100%",
          ease: "none"
        }
      });
      welcome.to(imageHeaderAnim, { y: "20vh"});
    }, 500)
  }

  setTimeout(() => document.body.classList.remove('act'), 1500)
}

export function Item({ id }) {

  const [itemsData, isLoaded, error] = useDataJson(blogItems.url);

  // ----------- Page navigation

  return (
    <>
      {(error) ? <div style={styleDataJson.loadError}>Error:{error.message}</div> : (!isLoaded) ? <div style={styleDataJson.loadError}><p>Loading Data...</p></div> :
        <>
          <div className="card-content-container" style={{marginTop: '50vh' }}>
            <div className="img-cont-anim">
              {/* <Link className="link-img-cont" to="/blog/" data-crsr-back /> */}
              <div className="img-cont" style={{ backgroundImage: `url(/assets/img/${id}.jpg)` }} />
            </div>
            {itemsData.map(item => {
              const { category, title, text, text2, images } = item;

              const crsrPrev = document.querySelector('.crsr-prev');

              if (item.id === id) {

                const pagePrev = () => {
                  let pageSrc = (id.substr(4) * 1) + 1;
                  if (pageSrc > itemsData.length) {
                    return ``
                  } else {
                    return `p-n/p-00${pageSrc}`
                  }
                };
                const pageNext = () => {
                  let pageSrc = (id.substr(4) * 1) - 1;
                  if (pageSrc === 0) {
                    return ``
                  } else {
                    return `p-n/p-00${pageSrc}`
                  }
                };

                return (
                  <div key={id} className="container" style={{ color: '#f9f9f9' }} ref={() => animaHeaderElements()}>
                    <span className="category">{category}</span>
                    <h2 className="title-header-anim" style={{ margin: 'calc(30px + 4vw) 0' }}>{title}</h2>
                    <div className="page-content">
                      <p>{text}</p>
                      {(images) ? images.map((value, index) => (
                        <div key={index} style={{ margin: 'calc(15px + 2vw) 0' }}>
                          <div style={{ position: 'relative', width: '100%', height: '35vw', minHeight: '300px', overflow: 'hidden', pointerEvents: 'none' }}>
                            <img src={value.src} alt={value.alt} style={{ width: '100%', height: '100%', position: 'absolute', left: 0, top: 0, overflow: 'hidden', objectFit: 'cover', background: '#bd9165' }} data-crsr-zoomout />
                          </div>
                          <p>{value.text}</p>
                        </div>
                      )) : null}
                      <p>{text2}</p>
                    </div>
                    <div className="content-pagination">
                      <Link to={`/blog/${pagePrev()}`} className="crsr-prev" data-crsr-prev><h2>PREV</h2></Link>
                      <Link to="/blog/" data-crsr-back><h3>BLOG</h3></Link>
                      <Link to={`/blog/${pageNext()}`} data-crsr-next><h2>NEXT</h2></Link>
                      {/* <button onClick={() => pageNext()} data-crsr-next>NEXT</button> */}
                    </div>
                  </div>
                )
              }
              return null
            })}
          </div>
        </>
      }
    </>
  )
}
