import React, { useEffect, useRef } from "react";
import "../home/home.css";
import "./assets/style.css";

import { Item } from "./assets/Item";
import { List } from "./assets/List";
import Footer from "../../components/footer/Footer";

import { Route, Switch } from "react-router-dom";
import { NoMatchPage } from "../../App";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";
import { HoverCursor } from "../../components/CustomCursor/CursorHoverSeiitngs";
import { ScrollCardSkew } from "./assets/ScrollCardSkew";

function Store({ match }) {
    let { id } = match.params;
    const imageHasLoaded = true;
    return (
        <>
            <List selectedId={id} />
            {id && imageHasLoaded && <Item id={id} key="item" />}
        </>
    );
}

function Works() {

    gsap.registerPlugin(ScrollTrigger);

    const LScroll = LocoScroll();
    const zagAnim = useRef();

    useEffect(() => {
        zagAnim.current.style.opacity = 0;
        zagAnim.current.style.transform = "scale(1.2)";
        setTimeout(() => {
            gsap.to(zagAnim.current, 1.4, {scale: 1, opacity: 1, ease: "Power4.easeOut" })
        }, 800);

        const welcome = gsap.timeline({
            scrollTrigger: {
                trigger: ".blue",
                scroller: ".sth-scrl",
                scrub: true,
                start: "top top",
                end: "+=120%",
                ease: "none"
            }
        });
        welcome.to(zagAnim.current, { y: "120%" });

        ScrollCardSkew();

        ScrollTrigger.refresh();

    }, [])

    useEffect(() => HoverCursor(), [])

    return (
        <div className="sth-scrl" lscroll={LScroll}>
            <section className="panel blue" style={{ height: '30vw', minHeight: '350px' }}>
                <div className="wel-h2">
                    <h2 className="bp-h2" ref={zagAnim}>Blog</h2>
                </div>
            </section>
            <div className="container">
                <Switch>
                    <Route exact path="/blog/" component={Store} />
                    {/* <Route exact path={["/saa/blog/id/:id", "/saa/blog/"]} component={Store} /> */}
                    <Route component={NoMatchPage} />
                </Switch>
            </div>
            <Footer />
        </div>
    )
}

export default Works;