import React, { useEffect, useRef, useState } from "react";
import "./home.css";

import { BrowserView, MobileView, isBrowser, isMobile } from "react-device-detect";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";
import { HoverCursor } from "../../components/CustomCursor/CursorHoverSeiitngs";

import img7 from "./testpageImg/7.jpg";
import { PagePagination } from "./PagePagination"
import Footer from "../../components/footer/Footer";

import { useDataJson, styleDataJson, blogItems } from "../../hooks/useDataJson";

import { Link } from "react-router-dom";

gsap.registerPlugin(ScrollTrigger);

function Home() {

    const [itemsData, isLoaded, error] = useDataJson(blogItems.url);
    const [contLoaded, setcontLoaded] = useState(false);

    /////////////////////////   Page scroll animation

    const LScroll = LocoScroll();
    const scrollDown = useRef(null);

    useEffect(() => {

        HoverCursor();

        if (contLoaded) {
            setTimeout(() => {
                if (scrollDown) {
                    welcome.to(scrollDown.current, { opacity: 0, duration: 1 }, '-=6');
                }
            }, 700);
            const welcome = gsap.timeline({
                scrollTrigger: {
                    trigger: ".blue",
                    scroller: ".sth-scrl",
                    scrub: true,
                    // pin: true,
                    // markers: true,
                    start: "top top",
                    end: "+=120%",
                    ease: "none"
                }
            });
            welcome
                .to('.bp-1', { y: "-26%", scale: 1.4, duration: 6 })
                .to('.bp-2', { scale: 1.2, y: "-3%", duration: 6 }, '-=6')
                .to('.bp-4', { y: "30%", duration: 6 }, '-=6')
                .to('.bp-3', { scale: 1.1, y: "15%", duration: 6 }, '-=6')
                .to('.bp-h2', { y: "20vh", duration: 6 }, '-=6')
                .to('.bp-h4', { y: "22vh", duration: 6 }, '-=6');

            gsap.from(".line-1", {
                scrollTrigger: {
                    trigger: ".line-1",
                    scroller: ".sth-scrl",
                    scrub: true,
                    start: "top bottom",
                    end: "top top"
                },
                scaleX: 0,
                transformOrigin: "left center",
                ease: "none"
            });

            if (isBrowser) {
                const t3 = gsap.timeline({
                    scrollTrigger: {
                        trigger: ".orange",
                        scroller: ".sth-scrl",
                        scrub: true,
                        pin: true,
                        start: "top top",
                        invalidateOnRefresh: true,
                        end: "+=100%"
                    }
                });

                t3.from(".line-2", { scaleX: 0, transformOrigin: "left center", ease: "none", duration: 6 }, '-=6')
                    .fromTo(".bp-7", { scale: 1.5, opacity: 0 }, { scale: 1, opacity: 1, ease: "none", duration: 6 }, '-=6');
                gsap.to(".bp-7", {
                    scrollTrigger: {
                        trigger: "#sectionPin",
                        scroller: ".sth-scrl",
                        scrub: true,
                        start: "top bottom",
                        end: "top top"
                    },
                    y: '30%', ease: "none"
                }, '-=6');

                let pinWrap = document.querySelector(".pin-wrap");
                gsap.to(pinWrap, {
                    scrollTrigger: {
                        scroller: ".sth-scrl",
                        scrub: true,
                        trigger: "#sectionPin",
                        pin: true,
                        invalidateOnRefresh: true,
                        // anticipatePin: 1,
                        start: "top top",
                        end: () => "+=" + pinWrap.offsetWidth / 1.5
                    },
                    x: () => -(pinWrap.scrollWidth - document.documentElement.clientWidth) + "px",
                    ease: "none",
                });

                let proxy = { skew: 0 },
                    skewSetter = gsap.quickSetter(".skewElem", "skewX", "deg"),
                    clamp = gsap.utils.clamp(-20, 20);

                ScrollTrigger.create({
                    scroller: ".sth-scrl",
                    trigger: "#sectionPin",
                    start: "top top",
                    end: () => "+=" + pinWrap.offsetWidth / 1.5,
                    onUpdate: self => {
                        let skew = clamp(self.getVelocity() / -300);
                        if (Math.abs(skew) > Math.abs(proxy.skew)) {
                            proxy.skew = skew;
                            gsap.to(proxy, { skew: 0, duration: 0.3, ease: "power3", overwrite: true, onUpdate: () => skewSetter(proxy.skew) });
                        }
                    }
                });
                gsap.set(".skewElem", { force3D: true }); // - fix 
            }
        }

        ScrollTrigger.refresh();
    }, [contLoaded]);

    return (
        <div className="sth-scrl" ref={LScroll}>
            <BrowserView>
                <PagePagination />
            </BrowserView>
            <section id="home" className="panel blue pre-anim">
                <div className="wel-h2 skewElem">
                    <div>
                        <h2 className="bp-h2 bp-h2-w">Wonderful<br />Architecture</h2>
                        <h4 style={{ textAlign: 'right' }} className="bp-h4 bp-h4-w">History Of City Architecture</h4>
                    </div>
                    <MobileView>
                        <div className="scroll-down" ref={scrollDown}>Scroll down</div>
                    </MobileView>
                </div>
                <div className="wel-h2-d">
                    <div className="bp bp-1"></div>
                    <div className="bp bp-2"></div>
                    <div className="bp bp-3"></div>
                    <div className="bp bp-4"></div>
                </div>
                <BrowserView>
                    <div className="scroll-down" ref={scrollDown}>Scroll down</div>
                </BrowserView>
            </section>
            <section className="panel red" >
                <div className="container">
                    <p><span className="line line-1"></span><br />This line's animation will begin when it enters the viewport and finish when its top edge hits the top of the viewport, staying perfectly in sync with the scrollbar because it has <code>scrub:&nbsp;true</code></p>
                </div>
            </section>
            <div style={{ position: 'relative', overflowX: "hidden" }}>
                <section className="panel orange" id="quote">
                    <img className="bp-7" src={img7} alt="description of" />
                    <div style={{ position: 'relative', zIndex: 2 }}>
                        <div className="container">
                            <p><span className="line line-2"></span><br />This orange panel gets pinned when its top edge hits the top of the viewport, then the line's animation is linked with the scroll position until it has traveled 100% of the viewport's height (<code>end: "+=100%"</code>), then the orange panel is unpinned and normal scrolling resumes. Padding is added automatically to push the rest of the content down so that it catches up with the scroll when it unpins. You can set <code>pinSpacing: false</code> to prevent that if you prefer.</p>
                        </div>
                    </div>
                </section>
            </div>

            <section id="sectionPin">
                <div className="pin-wrap">
                    <BrowserView>
                        <div style={{ width: '60vw' }}><h2>This orange panel gets pinned when its top edge hits the top of the viewport</h2></div>
                    </BrowserView>
                    <MobileView>
                        <h2>This orange panel gets pinned when its top edge hits the top of the viewport</h2>
                    </MobileView>
                    {(error) ? <div ref={() => setcontLoaded(true)} style={styleDataJson.loadError}>Error:{error.message}</div> : (!isLoaded) ? <div style={styleDataJson.loadError}><h4>Loading Data...</h4></div> :
                        <>
                            {itemsData.slice(-4).reverse().map(item => {
                                const { id, title, titleimage } = item;
                                return (
                                    <div key={id} style={{ position: 'relative' }} ref={() => setcontLoaded(true)}>
                                        <div className="img-hor skewElem" data-crsr-eye>
                                            <Link to={`/blog/p-n/${id}`}>
                                                <div className="img-cont" style={{ backgroundImage: `url(${titleimage})` }} />
                                            </Link>
                                        </div>
                                        <h3 className="skewElemPrlx">{title}</h3>
                                    </div>
                                )
                            }
                            )}
                        </>
                    }
                    <BrowserView>
                        <Link to="/blog" data-crsr-more>
                            <div className="img-hor" style={{ width: '30vw', marginRight: '10vw' }}>
                                <h2>and More</h2>
                            </div>
                        </Link>
                    </BrowserView>
                </div>
            </section>
            <section className="panel gray" id="done">
                WELL DONE!
            </section>
            <Footer />
        </div>
    )
}

export default Home;