import React, { useEffect, useRef } from "react";
import "../about/style.css";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";

import Footer from "../../components/footer/Footer";
import contactImg1 from "../../components/navigation/images/image4.jpg";

import { DataJson } from "../../components/datajson/data";

gsap.registerPlugin(ScrollTrigger);

function Contact() {

    const LScroll = LocoScroll();
    const zagAnim = useRef();

    useEffect(() => {
        zagAnim.current.style.opacity = 0;
        zagAnim.current.style.transform = "scale(1.2)";
        setTimeout(() => {
            gsap.to(zagAnim.current, 1.4, {scale: 1, opacity: 1, ease: "Power4.easeOut" })
        }, 800);
    });

    useEffect(() => {

        const welcome = gsap.timeline({
            scrollTrigger: {
                trigger: ".blue",
                scroller: ".sth-scrl",
                scrub: true,
                // pin: true,
                // markers: true,
                start: "top top",
                end: "+=130%",
                ease: "none"
            }
        });

        welcome.to('.bp-h2', { y: "13vh", duration: 4 }, '-=6');
        welcome.fromTo('.img-cont-1', { y: "-20%", scale: 1.3 }, { y: "10%", scale: 1, duration: 6 }, '-=6');

        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: ".purple",
                scroller: ".sth-scrl",
                scrub: true,
                pin: true,
                start: "top top",
                end: "+=100%"
            }
        });

        tl.from(".purple p", { scale: 0.3, rotation: 45, autoAlpha: 0, ease: "power2" })
            .from(".line-3", { scaleX: 0, transformOrigin: "left center", ease: "none" }, 0)
            .to(".purple", { backgroundColor: "#28a92b" }, 0);

        ScrollTrigger.refresh();

    }, [])

    return (
        <div className="sth-scrl" ref={LScroll}>
            <section className="panel blue" style={{ height: '70vh' }}>
                <div className="wel-h2">
                    <h2 className="bp-h2" ref={zagAnim}>Contact</h2>
                </div>
            </section>
            <div style={{ position: 'relative', overflowX: "hidden", width: "101.5%" }}>
                <section className="panel red" >
                    <img className="img-cont-1" src={contactImg1} alt="description of" />
                </section>
            </div>
            <section>
                <DataJson />
            </section>
            <Footer />
        </div>
    )
}

export default Contact;