import React from "react";
import "../home/home.css";
import { Route, Switch } from "react-router-dom";
import { NoMatchPage } from "../../App";
import LocoScroll from "../../hooks/useLocoScroll";
import { Item } from "./assets/Item";
import Footer from "../../components/footer/Footer";

function Works({ match }) {
    let { id } = match.params;
    const imageHasLoaded = true;
    return ((id && imageHasLoaded && <Item id={id} key="item" />))
}
function IdPage() {
    const LScroll = LocoScroll();
    return (
        <div className="sth-scrl" lscroll={LScroll}>
            <Switch>
                <Route exact path="/blog/p-n/:id" component={Works} />
                <Route component={NoMatchPage} />
            </Switch>
            <Footer />
        </div>
    )
}
export default IdPage;