import React, { useEffect, useRef } from "react";
import "./style.css";
import { gsap } from "gsap";

import { Link, useRouteMatch } from "react-router-dom";
import { isBrowser, isMobile } from "react-device-detect";

import { ContactNav } from "./Contact";
import { Toggle } from "../Toggle"

import img1 from "./images/image1.jpg";
import img2 from "./images/image2.jpg";
import img3 from "./images/image3.jpg";
import img4 from "./images/image4.jpg";

function OldSchoolMenuLink({ label, to, activeOnlyWhenExact }) {
    let match = useRouteMatch({
        path: to,
        exact: activeOnlyWhenExact
    });
    return (
        <Link className={match ? "active" : ""} to={to} data-crsr-zoomout >{label}</Link>
    );
}

const homeSpan = () => <h2><span>h</span><span>o</span><span>m</span><span>e</span></h2>;
const aboutSpan = () => <h2><span>a</span><span>b</span><span>o</span><span>u</span><span>t</span></h2>;
const worksSpan = () => <h2><span>b</span><span>l</span><span>o</span><span>g</span></h2>;
const contactSpan = () => <h2><span>c</span><span>o</span><span>n</span><span>t</span><span>a</span><span>c</span><span>t</span></h2>;
const homeLogo = () => <div className="logo" data-crsr-zoomin><svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 125.1 125.1"><polygon className="log-1" points="67.2,7.9 23.3,51.8 23.8,94.3 67.7,50.4 " /><polygon className="log-2" points="23.8,6.5 44.8,27.5 65.8,6.5" /><rect className="log-3" x="53.2" y="58.8" transform="matrix(0.7071 -0.7071 0.7071 0.7071 -32.2878 70.0452)" width="30.4" height="30.4" /><polygon className="log-4" points="90.8,75.9 48.1,118.6 90.8,118.6 " /><polygon className="log-5" points="111.8,51.1 90.8,30.1 69.8,51.1" /></svg></div>;

const Navigation = () => {

    const nmBtn = useRef(null);
    const navMenu = useRef(null);
    let navMenuC = useRef(null);
    const navMenuCa = useRef(null);
    const varyaLoh = useRef(null);

    useEffect(() => {
        const navMenuA = document.querySelectorAll('.nav-menu-l ul li a');
        const logo = document.querySelector('.logo');
        const bfix = document.body;
        varyaLoh.current.remove();

        nmBtn.current.addEventListener('click', () => {
            nmBtn.current.classList.toggle('act');
            navMenu.current.classList.toggle('act');
            bfix.classList.toggle('act');
        })

        const closeMenu = () => {
            let t1 = 700;
            if (isMobile) { t1 = 500 };
            setTimeout(() => {
                bfix.classList.remove('act');
                navMenu.current.className = "nav-menu no-act";
                nmBtn.current.className = "nm-btn no-act";
            }, t1);
            setTimeout(() => {
                navMenu.current.className = "nav-menu";
                nmBtn.current.className = "nm-btn";
            }, 1200);
        }

        navMenuA.forEach(i => i.addEventListener('click', closeMenu));
        logo.addEventListener('click', closeMenu);

        if (isBrowser) {
            navMenuA.forEach((event, image) => {
                event.addEventListener('mouseenter', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { scale: 1.4 }, { opacity: 1, scale: 1.1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 0;
                })
                event.addEventListener('mouseleave', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { opacity: 1, scale: 1.1 }, { opacity: 0, scale: 1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 1
                })
                event.addEventListener('click', () => {
                    navMenuCa.current.style.opacity = 0;
                })

                function navMediaArr() {
                    navMenuC.current.style.backgroundImage =
                        (image === 0) ? `url("${img1}")` :
                            (image === 1) ? `url("${img3}")` :
                                (image === 2) ? `url("${img2}")` :
                                    (image === 3) ? `url("${img4}")` : null;
                };

            });
        }

    }, [])

    return (
        <>
            <div className="navigation">
                <OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeLogo()} />
                <div className="nm-btn" ref={nmBtn} data-crsr-zoomin >
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 500 500"><polygon points="374.4,181.5 68.3,181.5 125.6,124.2 431.7,124.2 " /><polygon points="374.4,278.6 68.3,278.6 125.6,221.4 431.7,221.4 " /><polygon points="430,278.6 68.3,278.6 70,221.4 431.7,221.4 " /><polygon points="430,278.6 68.3,278.6 70,221.4 431.7,221.4 " /><polygon points="374.4,375.8 68.3,375.8 125.6,318.5 431.7,318.5 " /></svg>
                </div>
                <div className="nav-menu" ref={navMenu}>
                    <div className="nav-menu-l">
                        <ul>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/blog" label={worksSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/about" label={aboutSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/contact" label={contactSpan()} /></li>
                        </ul>
                    </div>
                    <Toggle />
                    <div className="nav-menu-c">
                        <div className="nav-menu-c-b" ref={navMenuC}>
                            <div ref={varyaLoh}>
                                <img src={img1} alt="home" />
                                <img src={img2} alt="blog" />
                                <img src={img3} alt="about" />
                                <img src={img4} alt="contact" />
                            </div>
                        </div>
                        <div className="nav-menu-c-a" style={{ padding: '0 20%', color: '#f9f9f9' }}>
                            <div ref={navMenuCa}>
                                <ContactNav />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Navigation;