import React from "react";
import { motion } from "framer-motion";
import { BrowserView, MobileView } from "react-device-detect";

export const PageMotion = ({ children }) => {

    return (
        <>
            <BrowserView className="m-page-cont">
                <div className="m-page-box">
                    <motion.span className="m-page-r" initial={{ y: '0' }} animate={{ y: '100%', transitionEnd: { display: 'none' } }} transition={{ duration: .5, delay: .3, ease: [0.7, 0, 0.3, 1] }} exit={false} />
                    <motion.span className="m-page-r" initial={false} animate={{ y: '-100%', display: 'none' }} transition={{ duration: .7, ease: [0.7, 0, 0.3, 1] }} exit={{ y: '0%', display: 'block' }} />
                </div>
                {children}
            </BrowserView>
            <MobileView className="m-page-cont">
                <div className="m-page-box">
                    <motion.span className="m-page-r" initial={{ x: '0' }} animate={{ x: '-100%', transitionEnd: { display: 'none' } }} transition={{ duration: .5, delay: .2 }} exit={false} style={{ width: '100%' }} />
                    <motion.span className="m-page-r" initial={false} animate={{ x: '100%', display: 'none' }} transition={{ duration: .5 }} exit={{ x: '0%', display: 'block' }} style={{ width: '100%' }} />
                </div>
                {children}
            </MobileView>
        </>
    );
}