import { useEffect } from "react";
import { isBrowser } from "react-device-detect";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import LocomotiveScroll from "locomotive-scroll";

gsap.registerPlugin(ScrollTrigger);

const LocoScroll = (locoScroll) => {

  useEffect(() => {

    let locoScroll = null;
    const scrollEl = document.querySelector(".sth-scrl");

    locoScroll = new LocomotiveScroll({
      el: scrollEl,
      smooth: true,
      lerp: 0.06,
      direction: 'vertical',
      reloadOnContextChange: true,
      scrollbarClass: 'scrollbar',
      repeat: true,
      scrollbarContainer: false,
      tablet: {
        el: scrollEl,
        smooth: false,
        direction: 'vertical',
        gestureDirection: 'vertical',
        breakpoint: 1024,
      },
      smartphone: {
        el: scrollEl,
        smooth: false,
        direction: 'vertical',
        gestureDirection: 'vertical',
      }
    });

    locoScroll.on("scroll", ScrollTrigger.update);

    locoScroll.update();
    window.scroll = locoScroll;

    ScrollTrigger.scrollerProxy(scrollEl, {
      scrollTop(value) {
        if (locoScroll) {
          scrollEl.style.overflowY = 'hidden';
          return arguments.length
            ? locoScroll.scrollTo(value, 0, 0)
            : locoScroll.scroll.instance.scroll.y;
        }
        return null;
      },
      scrollLeft(value) {
        if (locoScroll) {
          scrollEl.style.overflowY = 'hidden';
          return arguments.length
            ? locoScroll.scrollTo(value, 0, 0)
            : locoScroll.scroll.instance.scroll.x;
        }
        return null;
      },
      getBoundingClientRect() {
        return {
          top: 0,
          left: 0,
          width: window.innerWidth,
          height: window.innerHeight
        };
      },

      pinType: scrollEl.style.transform ? "transform" : "fixed"
    });

    const lsUpdate = () => {
      if (locoScroll) {
        locoScroll.update();
      }
    };

    if (isBrowser) { // update scroll
      const config = { subtree: true, childList: true };
      const observer = new MutationObserver(mutation => (mutation.type === 'childList') ? lsUpdate() : (mutation.type === 'subtree') ? lsUpdate() : lsUpdate());
      observer.observe(scrollEl, config);
      // setTimeout(() => lsUpdate(), 300);
      // document.querySelector("img").onload = () => lsUpdate();
    }

    ScrollTrigger.addEventListener("refresh", lsUpdate);
    ScrollTrigger.refresh();

    return () => {
      if (locoScroll) {
        ScrollTrigger.removeEventListener("refresh", lsUpdate);
        locoScroll.destroy();
        locoScroll = null;
        // console.log("Kill", locoScroll);
      }
    };
  },[]);
}

export default LocoScroll;