import React from "react";

export const PagePagination = () => {
    return (
        <>
            <div id="p--m-t" style={{ position: 'fixed', top: 0, height: '10000vh' }}></div>
            <div className="page-nav" data-scroll data-scroll-sticky data-scroll-target="#p--m-t" data-hover-btn>
                <div className="page-nav-a" data-crsr-zoomout>
                    <a href="#home" data-scroll data-scroll-to data-scroll-class="active" data-scroll-repeat data-scroll-target="#home">btn</a>
                    <a href="#quote" data-scroll data-scroll-to data-scroll-class="active" data-scroll-repeat data-scroll-target="#quote">btn</a>
                    <a href="#sectionPin" data-scroll data-scroll-to data-scroll-class="active" data-scroll-repeat data-scroll-target="#sectionPin">btn</a>
                    <a href="#done" data-scroll data-scroll-to data-scroll-class="active" data-scroll-repeat data-scroll-target="#done">btn</a>
                </div>
            </div>
        </>
    )
}