import { useState, useEffect } from "react";

export const blogItems = { url: "/iws/assets/json/bl23o3g_ite23m2s.json" }

const styleDataJson = {
  loadError: {
    position: 'relative',
    top: 0,
    left: 0,
    width: '100%',
    minWidth: '50%',
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: '#f9f9f9',
    zIndex: 4
  }
}

function useDataJson(url) {

  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [itemsData, setItems] = useState([]);

  useEffect(() => {
    let controller = new AbortController();
     fetch(url, {
      method: 'GET',
      mode: 'cors',
      cache: 'default',
      // cache: 'no-cache',
      // cache: 'force-cache',
      signal: controller.signal,
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )

  }, [url]);

  return [itemsData, isLoaded, error];
}
export { useDataJson, styleDataJson };