import React from "react";
import { style } from "./data"

export const TableJson = ({ sortData, itemData }) => {
    return (
        <>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
                <div style={style.jsoncontR} onClick={() => sortData('name')}><h4>name</h4></div>
                <div style={style.jsoncontR} onClick={() => sortData('company')}><h4>company</h4></div>
                <div style={style.jsoncontR} onClick={() => sortData('email')}><h4>email</h4></div>
                <div style={style.jsoncontR} onClick={() => sortData('phone')}><h4>phone</h4></div>
                <div style={style.jsoncontR} onClick={() => sortData('address')}><h4>address</h4></div>
            </div>
            {itemData.map(arr =>
                <div key={arr._id} style={{ display: 'flex', flexDirection: 'row', alignItems: 'stretch' }}>
                    <div style={style.jsoncontL}>
                        <div style={style.jsonText}><b>{arr.name}</b></div>
                    </div>
                    <div style={style.jsoncontL}>
                        <div style={style.jsonText}>{arr.company}</div>
                    </div>
                    <div style={style.jsoncontL}>
                        <div style={style.jsonText}>{arr.email}</div>
                    </div>
                    <div style={style.jsoncontL}>
                        <div style={style.jsonText}>{arr.phone}</div>
                    </div>
                    <div style={style.jsoncontL}>
                        <div style={style.jsonText}>{arr.address}</div>
                    </div>
                </div>
            )}
        </>
    )
}