import React, { useState, useEffect } from 'react';

// import { gsap } from "gsap";
// import { ScrollTrigger } from "gsap/ScrollTrigger";

const style = {
    loadError: {
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100vh',
        background: '#0d0d06',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 20
    },
    container: {
        padding: 'calc(20px + 5vw) 0',
    },
    containerImage: {
        position: 'relative',
        width: "100%",
        height: "60vh",
        overflow: "hidden",
        display: 'flex',
        justifyContent: "center",
        alignItems: "center",
        background: '#bd9165',
    },
    image: {
        width: '100%',
        height: '100%',
        position: 'absolute',
        objectFit: 'cover',
    }

}

export const TestJSON = () => {

    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [items, setItems] = useState([]);

    useEffect(() => {
        fetch("assets/json/data.json", {
            method: 'GET',
            mode: 'cors',
            cache: 'no-cache',
            credentials: 'same-origin',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then(res => res.json())
            .then(
                (result) => {
                    setIsLoaded(true);
                    setItems(result);
                },
                (error) => {
                    setIsLoaded(true);
                    setError(error);
                }
            )
    }, [])

    if (error) {
        return <div style={style.loadError}>Error:{error.message}</div>
    } else if (!isLoaded) {
        return <div style={style.loadError}><p>Loading Data...</p></div>;
    } else {
        return (
            <div className="test-json">
                <div className="t-json" style={style.container}>
                    {items.map(arr =>
                        arr.aboutpage.map(item =>
                            <div key={item._id}>
                                <div className="container">
                                    {(item.company) ? <h2 style={{ textAlign: "center" }}>{item.company}</h2> : null}
                                    <p>{item.about}</p>
                                </div>
                                {(item.image || item.imagetext) ?
                                    <>
                                        <div style={style.containerImage}><img style={style.image} src={item.image} alt={item.company} /></div>
                                        <div className="container">{`Text image: ${item.imagetext}`}</div>
                                    </> : null}
                            </div>
                        )
                    )}
                </div>
            </div>
        );
    }
}
