import React, { useRef, useEffect } from 'react';

export const Toggle = () => {
  const toggleColorBtn = useRef();
  const bodyTheme = document.body;
  const colorLocal = localStorage.getItem("color");

  bodyTheme.className = colorLocal;

  const toggleColor = () => {
    if (toggleColorBtn.current.className === "theme-app black") {
      toggleColorBtn.current.className = "theme-app white";
      bodyTheme.classList.remove("black");
      bodyTheme.classList.add("white");
      localStorage.setItem("color", "white");
    } else if (toggleColorBtn.current.className === "theme-app white") {
      toggleColorBtn.current.className = "theme-app black";
      bodyTheme.classList.remove("white");
      bodyTheme.classList.add("black");
      localStorage.setItem("color", "black");
    } else {
      console.log('dark light: error')
    }
  };

  useEffect(() => {
    if (toggleColorBtn.current.className === "theme-app null") {
      toggleColorBtn.current.className = "theme-app black"
    }
  }, [])

  return (
    <div ref={toggleColorBtn} className={`theme-app ${colorLocal}`} onClick={() => toggleColor()} data-crsr-zoomin />
  )
};