import React, { useState, useEffect, useRef } from "react";
import { BrowserView, MobileView } from "react-device-detect";
import { useDataJson, styleDataJson, blogItems } from "../../../hooks/useDataJson";
import { Link } from "react-router-dom";
import { BlogNavigation } from "./BlogNavigation";
import { HoverCursor } from "../../../components/CustomCursor/CursorHoverSeiitngs";
import { ScrollCardSkew } from "./ScrollCardSkew";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);


const HoverAndScroll = () => {
  ScrollCardSkew();
  HoverCursor()
}

function Card({ id, title, category, titlettemple, texttemple }) {

  const cardContent = useRef();

  return (
    <div className="card">
      <div ref={cardContent} className="card-content-container card-anim">
        <div className="card-content">
          <div className="card-image-container skewElem">
            <Link to={`/blog/p-n/${id}`} className={`card-open-link`}>
              <div className="card-image" style={{ backgroundImage: `url(/assets/img/${id}.jpg)` }} data-crsr-eye />
              <BrowserView>
                <span className="title-category">{category}</span>
                <div className="title-text" >
                  <div>
                    <h4>{titlettemple}</h4>
                    <p>{`${texttemple}...`}</p>
                    <hr style={{ margin: "1vw 0" }} />
                    {/* <h5>read more</h5> */}
                  </div>
                </div>
              </BrowserView>
            </Link>
          </div>
          <div className="title-container" style={{ marginBottom: 'calc(20px + 3vw)' }}>
            <h3 style={{ zIndex: 3 }}>{title}</h3>
          </div>
        </div>
      </div>
    </div>
  )
}

//////////////////// load more


export function List({ selectedId }) {

  const [itemsData, isLoaded, error] = useDataJson(blogItems.url);

  // let arrayForHoldingStories = [];
  const copyItems = itemsData.concat();
  const [storiesToShow, setStoriesToShow] = useState([]);
  const [next, setNext] = useState(7);
  const loadMoreBtn = useRef();
  const storiesPerPage = 7;
  // const [storiesToShowCopy, setStoriesToShowCopy] = useState(true);

  const loopWithSlice = () => {
    const toShow = copyItems.slice(
      storiesToShow.length,
      storiesToShow.length + storiesPerPage
    );
    setStoriesToShow([...storiesToShow, ...toShow]);
  };

  useEffect(() => {
    if (itemsData) { setStoriesToShow(itemsData.reverse().slice(0, storiesPerPage)); }
  }, [storiesPerPage, itemsData]);

  const handleShowMorePosts = () => {
    let loadedMore = next + storiesPerPage;
    loopWithSlice(next, loadedMore);
    setNext(next + storiesPerPage);
    (storiesToShow[copyItems.length - storiesPerPage]) ? loadMoreBtn.current.style.display = "none" : loadMoreBtn.current.style.display = "flex";
  };

  return (
    <>
      {(error) ? <div style={styleDataJson.loadError}>Error:{error.message}</div> : (!isLoaded) ? <div style={styleDataJson.loadError}><p>Loading Data...</p></div> :
        <>
          <BlogNavigation setStoriesToShow={setStoriesToShow} storiesPerPage={storiesPerPage} loadMoreBtn={loadMoreBtn} copyItems={copyItems} />
          <div className="blog-list" ref={() => HoverAndScroll()}>
            {storiesToShow.length > 0 && storiesToShow.map((cardItem, index) => <Card key={index} {...cardItem} isSelected={cardItem.id === selectedId} />)}
          </div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <MobileView>
              <button ref={loadMoreBtn} className="load-more-btn" onClick={handleShowMorePosts}>load more</button>
            </MobileView>
            <BrowserView>
              <h2 ref={loadMoreBtn} className="load-more-btn dekstop-load-more" onClick={handleShowMorePosts} data-crsr-more>more</h2>
            </BrowserView>
          </div>
        </>}
    </>
  )
}
