import React, { useState, useEffect } from 'react';
import { TableJson } from './table';

export const style = {
    loadError: { position: 'relative', top: 0, left: 0, width: '100%', height: 'auto', minHeight: '200px', background: '#222', color: '#f9f9f9', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: 2 },
    jsoncont: { position: 'relative', width: '65%', height: 'auto', display: 'flex', flexDirection: 'column', alignItems: 'stretch', left: '50%', transform: 'translateX(-50%)', marginBottom: '5%', transition: '0.8s ease' },
    jsoncontR: { width: '100%', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', background: '#f9f9f9', margin: '1px', cursor: 'pointer' },
    jsoncontL: { color: '#f9f9f9', width: '100%', display: 'flex', justifyContent: 'center' },
    jsonText: { padding: '20px 10px', width: '100%', textAlign: 'center', margin: '1px', background: '#666' }
}

export const DataJson = () => {

    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [itemData, setItems] = useState([]);
    const [directionSort, setdirectionSort] = useState(true);

    const sortData = field => {
        const copyData = itemData.concat();
        let copyDataSort;
        if (directionSort) {
            copyDataSort = copyData.sort((a, b) => (a[field] > b[field]) ? 1 : -1)
        } else {
            copyDataSort = copyData.sort((a, b) => (a[field] < b[field]) ? 1 : -1)
        }
        setItems(copyDataSort)
        // console.log(copyDataSort)
        setdirectionSort(!directionSort)
        // console.log(directionSort, copyDataSort)
    }

    useEffect(() => {
        fetch("./assets/json/generated.json", {
            method: 'GET',
            mode: 'cors',
            cache: 'no-cache',
            credentials: 'same-origin',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then(res => res.json())
            .then(
                (result) => {
                    setIsLoaded(true);
                    setItems(result);
                },
                (error) => {
                    setIsLoaded(true);
                    setError(error);
                }
            )
    }, [])

    if (error) {
        return <div style={style.loadError}>Error:{error.message}</div>
    } else if (!isLoaded) {
        return (
            <div style={style.jsoncont}>
                <div style={style.loadError}><p>Loading Data...</p></div>
            </div>
        );
    } else {
        return (
            <div style={style.jsoncont}>
                <TableJson itemData={itemData} sortData={sortData} />
            </div>
        );
    }
}
